body{font-family:tahoma,나눔고딕;background:#eae9ff;color:#45385a}
h1,h2,h3,h4,h5,h6{margin:0;font-weight:normal;}

.App{padding:1.0rem;
  position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);
  box-sizing:border-box;width:700px;
  background-image:linear-gradient(#d4befb,#8cadff);box-shadow:3px 3px 10px 0 #333;border-radius:10px;border:1px solid rgba(255, 255, 255, 0.5);
}

.App-header h1{
  margin:0 0 1.0rem 1.0rem;font-weight:600;text-shadow:1px 1px 2px #fff;
}
.App-header h1 button{
  float:right;
}
.todo-list h1{font-size:5.0rem;text-shadow:1px 1px 1px #fff;}
.todo-list h5{font-size:1.0rem;text-shadow:1px 1px 1px #fff;margin-top:-10px;text-transform:uppercase;}
.todo-list h1,.todo-list h5{text-align:center;}
.todo-input{width:90%;margin:1.0rem auto;}
.todo-input input{float:left;
  box-sizing:border-box;border:1px solid #fff;background:#fff;color:#000;text-shadow:none;box-shadow:1px 1px 5px #999,1px 1px 1px #bbb inset;
  width:80%;height:34px;padding:0 15px;border-radius:0.5rem 0 0 0.5rem;
}
.todo-input input:focus{outline:1px solid #408dfd;}

.todo-input button.btn-add{box-sizing:border-box;padding:0 15px;width:20%;height:36px;border-radius:0 0.5rem 0.5rem 0;font-weight:500;font-size:15px;cursor:pointer;
background:#408dfd;color:#fff;border:1px solid #408dfd;box-shadow:1px 1px 5px #777,-1px -1px 1px #333 inset,1px 1px 1px #abcdff inset;}
.todo-input button.btn-add:hover {
background:#408dfd;box-shadow:none;box-shadow:1px 1px 5px #777;}
.todo-input button.btn-add:active {
background:#408dfd;box-shadow:1px 1px 5px #777,2px 2px 3px #666 inset;}

.todo-list ul{width:90%;margin:0 auto 1.0rem auto;}
.todo-list ul,.todo-list li{list-style-type:none;padding:0;}
.todo-list ul li{
  border-radius:0.5rem;font-size:14px;padding:0 0 0 10px;margin-bottom:0.2rem;
  background:#7063fa;color:#fff;box-shadow:1px 1px 5px rgba(0,0,0,.5) inset;
}
.todo-list ul li span{float:left;display:block;width:250px;line-height:30px;margin:5px 0;
}
.todo-list ul li button{float:right;display:block;margin:5px 5px 0 0;padding:0 0.5rem;height:30px;border:none;
  border-radius:0.3rem;font-weight:600;font-size:14px;
  cursor:pointer;
  background:#ffc741;color:#473305;border:1px solid rgba(255,255,255,0.3);}
.todo-list ul li button:hover {
  background:#ffd05f;}
.todo-list ul li button:active {
  background:#ffd05f;}

.todo-list ul li::after{
  content:'';
  clear:both;
  display:block;
}

.week-calendar{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;  /* grid-gap is replaced by gap */
}

.day-container:first-child{color:crimson;}
.day-container:last-child{color:rgb(126, 79, 255);}

.day-container{
  min-height:130px;
  box-sizing: border-box;
  font-size:13px;
  border-radius:0.5rem;
  padding:0.5rem;
  background:rgba(255, 255, 255, 0.5);
  border:1px solid #fff;
  box-shadow:1px 1px 5px #999;
  transition: all 0.3s;  /* Smooth transition */
}

.day-container.today {
  border:1px solid rgb(255, 71, 71);
  background:rgba(255, 135, 135, 0.3);
}

.day-container.selected {
  z-index: 1;  /* Make it appear on top */
  grid-row-start: span 2;  /* It will span two rows */
  grid-column-start: span 2;  /* It will span two columns */
}
.day-container:nth-child(1).selected{
  transform: scale(1.2) translate(8.3%,8.3%);
}
.day-container:nth-child(2).selected{
  transform: scale(1.2) translate(-8.3%,8.3%);
}
.day-container:nth-child(3).selected{
  transform: scale(1.2) translate(8.3%,-8.3%);
}
.day-container:nth-child(4).selected{
  transform: scale(1.2) translate(8.3%,-8.3%);
}
.day-container:nth-child(5).selected{
  transform: scale(1.2) translate(-8.3%,-8.3%);
}
.day-container:nth-child(6).selected{
  transform: scale(1.2) translate(8.3%,-8.3%);
}
.day-container:nth-child(7).selected{
  transform: scale(1.2) translate(8.3%,-8.3%);
}

.day-container h4{font-weight:bold;margin-bottom:10px;}

.day-container ul.ul-cal,.day-container ul.ul-cal li{list-style-type:none;padding:0;margin:0;}
.day-container ul.ul-cal li{
  box-sizing:border-box;font-size:12px;
  line-height:22px;border-radius:0.2rem;padding:0 0 0 5px;margin-bottom:0.2rem;
  background:rgba(112, 99, 250, .5);color:#fff;width:198.67px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.bg-merryma{background:url(./images/rol.png) no-repeat right bottom;background-size:500px;}

.btn-clear{float:right;display:block;margin:5px 5px 0 0;padding:0 0.5rem;height:26px;border:none;
  border-radius:0.3rem;font-weight:600;font-size:14px;
  cursor:pointer;
  background:#e2e2e2;color:#333;border:1px solid #aaa;}
.btn-clear:hover {
  background:#f3f3f3;}
.btn-clear:active {
  background:#ffffff;}
  
@keyframes float{
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-30px);
  }
  100% {
    transform: translatey(0px);
  }
}

.fab {
  background-image:linear-gradient(to left top, #32146c, #32c2ca);
  box-sizing:border-box;border:none;box-shadow:3px 3px 10px rgba(0,0,0,.5);
  width:100px;height:100px;border-radius:50px;
  animation:float 5s ease-in-out infinite;
  position:fixed;top:50%;right:20px;
  margin-top:-50px;
  z-Index:100;cursor:pointer;
}
.fab div{
  display:block;height:100px;
  background:url(./images/test.png) no-repeat 50% 50%;background-size:40px;
  transition: background-size 0.1s ease;
}
.fab div span {
  scale: .1;
  position:absolute;top:-20px;left:-60px;
  background:#fff;
  width:80px;padding:10px;box-shadow:3px 3px 10px rgba(0,0,0,.5);
  box-sizing:border-box;border-radius:10px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease, scale 0.3s ease;
}
.fab:hover{
  box-shadow:none;
  box-shadow:3px 3px 10px rgba(0,0,0,.5);
}
.fab div:hover{
  background-size:60px
}
.fab:hover div span {
  scale: 1;
  opacity: 1;
  visibility: visible;
}